<template>
<div class="form">
   <form @submit.prevent="submit">
      <div><input
            placeholder="Imię"
            type="text" id="name"
            class="form-item"
            :class="{invalid: $v.name.$error}"
            v-model="name"
            @blur="$v.name.$touch()"
            ></div>
            <input
            placeholder="Nazwisko"
            type="text" id="surname"
            class="form-item"
            :class="{invalid: $v.surname.$error}"
            v-model="surname"
            @blur="$v.surname.$touch()">
            <input
            placeholder="Email"
            type="email" id="email"
            class="form-item"
            :class="{invalid: $v.email.$error}"
            v-model="email"
            @blur="$v.email.$touch()">
            <textarea
            placeholder="Wiadomość"
            id="message"
            class="form-item"
            :class="{invalid: $v.message.$error}"
            v-model="message"
            @blur="$v.message.$touch()"></textarea>
            <transition name="fade">
              <p v-if="$v.name.$error || $v.surname.$error|| $v.email.$error || $v.message.$error" class="form__warning">
                Uzupełnij brakujące pola
              </p>
            </transition>
            <button
                type="submit"
                class="btn">Wyślij
            </button>
    </form>
    <transition name="fade">
    <p class="form__send" v-if="formSend===true">Wiadomość została wysłana</p>
    </transition>
</div>
</template>
<script>
import { required, email} from 'vuelidate/lib/validators'
import axios from 'axios';
export default {
  data(){
    return{
      name:'',
      surname:'',
      email:'',
      message:'',
      formSend: false

    }
  },
   validations: {
    name: {
      required:required,
    },
     surname: {
      required:required,
    },
     email:{
        required:required,
        email:email
      },
      message:{
        required:required,
      },
   },
  methods:{
    submit(){
      this.$v.name.$touch();
      this.$v.surname.$touch();
      this.$v.email.$touch();
      this.$v.message.$touch();
      const client={
        name: this.name,
        surname: this.surname,
        email: this.email,
        message: this.message
      }
      if(this.$v.name.$error || this.$v.surname.$error|| this.$v.email.$error || this.$v.message.$error){
        this.formSend = false
      }
      else{
        axios.post(`https://adriancygnar-df0eb.firebaseio.com/client.json`, client)
      .then((response) => {
      console.log(response);
      if(response.status === 200){
        this.formSend = true
      }
      }, (error) => {
      console.log(error);
    });
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  form{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

  .form-item{
    background: #f8f8f8;
    color: #8792a1;
    margin: 10px 0;
    width:400px;
    height:40px;
    padding: 10px;
    border-bottom: 1px solid #8792a1;
    outline-color: #8792a1;
    transition: 0.5s ease;
  }

  textarea.form-item{
    height: 80px;
    width: 400px;
    resize: none;
  }
   @media(max-width: 560px){
    .form-item{
      width: 300px;
    }
    textarea.form-item{
      width: 300px;
    }
  }
    .btn{
      width: 100px;
      height: 40px;
      border-radius: 5px;
      text-align: center;
      line-height: 40px;
      background: #dfeef7;
      color: #8792a1;
      font-size: 16px;
      font-weight: 500;
      transition: 0.3s ease-in-out;
      margin:10px 0;
      cursor: pointer;
      &:hover{
        background: #95D3F9;
        color: #fff;
      }
    }
  }
  .invalid{
    border-bottom: 1px solid red !important;
    background: #FDC4C0!important;
  }
  .form__warning{
    margin: 10px 0;
    color: red;
    text-align: center;
    font-size: 14px;
    font-weight: 500;
  }
.form__send{
    margin: 10px 0;
    color: green;
    text-align: center;
    font-size: 14px;
    font-weight: 500;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .4s;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}
</style>